<template>
    <view class="text-header">
        <template v-for="(item, i) in option">
            <w-select 
            :show.sync="item.active" 
            :options="item.options || []"
            :type="item.type" 
            @isActive="handelIsActive(i)"
            @change="change($event, item.type)">
                <template v-if="item.type == 'fontSize'">
                    {{getOptionDict(item.options, value[item.type] || item.value)}}
                </template>
                <template v-else-if="item.type == 'color'">
                    <view 
                    class="color-box" 
                    :style="'background:' + (value[item.type] ||  item.value)">
                    </view>
                </template>
                <template v-else>
                    <i class="w_editor-icon" :class="[getOptionDict(item.options, value[item.type] || 'w_editor-icon-alignLeft')]"></i>
                </template>
            </w-select>
        </template>
    </view>
</template>

<script>
    import WSelect from '../components/w_select/index.vue'
    export default {
        components: {
            WSelect
        },
        props: {
            value: {
              type: Object,
              default: undefined,
            },
        },
        data() {
            return {
                active: -1,
                option: [
                    {
                        type: 'fontSize',
                        active: false,
                        value: '30px',
                        options: [
                            {
                                label: '14',
                                value: '14px'
                            },
                            {
                                label: '16',
                                value: '16px'
                            },
                            {
                                label: '18',
                                value: '18px'
                            },
                            {
                                label: '22',
                                value: '22px'
                            },
                            {
                                label: '26',
                                value: '26px'
                            },
                            {
                                label: '32',
                                value: '32px'
                            },
                            {
                                label: '40',
                                value: '40px'
                            },
                            {
                                label: '48',
                                value: '48px'
                            },
                            {
                                label: '60',
                                value: '60px'
                            }
                        ],
                    },
                    {
                        type: 'color',
                        active: false,
                        value: '#000',
                        options: [
                            {
                                label: '#000000',
                                value: '#000000'
                            },
                            {
                                label: '#888888',
                                value: '#888888'
                            },
                            {
                                label: '#d33127',
                                value: '#d33127'
                            },
                            {
                                label: '#f19e3c',
                                value: '#f19e3c'
                            },
                            {
                                label: '#ffff57',
                                value: '#ffff57'
                            },
                            {
                                label: '#40882b',
                                value: '#40882b'
                            },
                            {
                                label: '#2f63c5',
                                value: '#2f63c5'
                            },
                            {
                                label: '#8d39f6',
                                value: '#8d39f6'
                            }
                        ]
                    },
                    {
                        type: 'textAlign',
                        active: false,
                        value: 'left',
                        icon: 'alignLeft',
                        options: [
                            {
                                label: 'w_editor-icon-alignLeft',
                                value: 'left'
                            },
                            {
                                label: 'w_editor-icon-alignRight',
                                value: 'right'
                            },
                            {
                                label: 'w_editor-icon-center',
                                value: 'center'
                            },
                            {
                                label: 'w_editor-icon-alignBothSides',
                                value: 'justify'
                            }
                        ]
                    },
                ]
            }
        },
        mounted() {
            
        },
        methods: {
            handelIsActive(index) {
                const arr = [...this.option]
                arr.forEach(item => {
                    item.active = false
                })
                arr[index].active = true
                this.option = arr
            },
            getOptionDict(arr, value) {
                const find = arr.find(o => o.value == value) || {}
                return find.label
            },
            change(e, type) {
                const arr = [...this.option]
                arr.forEach(item => {
                    item.active = false
                })
                this.option = arr
                const obj = JSON.parse(JSON.stringify(this.value))
                obj[type] = e
                this.$emit('input', obj)
            },
        },
    }
</script>

<style lang="scss" scoped>
    
    
    .text-header{
        display: flex;
        align-items: center;
        .color-box{
            width: 30rpx;
            height: 30rpx;
            display: inline-block;
        }
    }
</style>